.menu-control {
    --menu-control-dim: 22px;
    --menu-control-border: 2px;

    border: none;
    width: var(--tap-region);
    height: var(--tap-region);
    background: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    .is-menu-open & {

    }

    @media (min-width: $mq__tablet) {
        display: none;
    }

    &__item {
        min-width: var(--menu-control-dim);
        width: var(--menu-control-dim);
        height: var(--menu-control-border);
        background: var(--color-ctrl);
        position: relative;

        &:before {
            bottom: 100%;
            margin-bottom: 4px;
        }
        &:after {
            top: 100%;
            margin-top: 4px;
        }

        &:before,
        &:after {
            content: '';
            position: absolute;
            display: block;
            width: var(--menu-control-dim);
            height: var(--menu-control-border);
            background: var(--color-ctrl);
        }
    }
}
